
import { defineComponent, computed, useCssVars } from "vue";
const component = defineComponent({
    name: "Icon",
    props: {
        name: {
            type: String,
            required: true
        }
    },
    setup(props) {
        const viewBox = computed(() => {
            switch (props.name) {
                case "empty":
                    return "0 0 59.227 59.227";
                default:
                    return "0 0 0 0";
            }
        });
        const pathTransform = computed(() => {
            if (props.name === "list") {
                return "translate(-100%, -120%)";
            } else {
                return "unset";
            }
        });
        useCssVars(() => ({ pathTransform: pathTransform.value }));
        return { viewBox, pathTransform };
    }
});
export default component;
